$primary-color: #7b5ae0 !default;
$primary-dark: darken($primary-color, 15%);

$main-font-family: Georgia, Palatino, "Palatino Linotype", Times,
  "Times New Roman", serif;
$mono-font: Menlo, Consolas, monospace;

$body-color: #202020;
$accent-grey: #999;
$light-grey: #f0f0f0;
$midtone-gray: #ccc;

$pre-sidebar-grey: rgba(153, 153, 153, 0.6);
$pre-background-grey: rgba(182, 182, 182, 0.09);

$default-font-size: 1em;
$line-height: 1.5em;

$meta-size: 0.82;
$meta-font-size: $meta-size * $default-font-size;
$meta-line-height: $meta-size * $line-height * 1.15;

$max-width: 750px !default;
$default-padding: 20px !default;
$big-font-size: 2em !default;

$sidebar-border-width: 3px;

$scaling-factor: 0.95;
$code-scaling-factor: 0.88;

$jekyll-red: #d50000;
$linode-green: green;
$github-grey: #171515;
$twitter-blue: #55acee;
